* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

html {
  font-family: Arial, Helvetica, "Lucida Sans", "Lucida Sans Regular",
    "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  background-color: rgb(240, 240, 240);
  font-size: 10px;
}

body {
  display: grid;
  place-items: center center;
}
.profile {
  width: 3em;
  height: 3em;
  border-radius: 50%;
}

.icon-bar {
  width: 90px;
  background-color: rgb(255, 255, 255);
  border-radius: 1em;
}

.icon-bar a {
  display: block; /* Make the links appear below each other instead of side-by-side */
  text-align: center; /* Center-align text */
  padding: 16px; /* Add some padding */
  transition: all 0.3s ease; /* Add transition for hover effects */
  color: gray; /* White text color */
  font-size: 36px; /* Increased font-size */
}

.icon-bar a:hover {
  background-color: rgb(221, 221, 221);
}

#active {
  color: rgb(72, 107, 214);
}
.plusIcon {
  color: rgb(72, 107, 214);
  padding-left: 1em;
}

.faCog {
  color: rgb(42, 42, 42);
  padding-left: 1em;
}
.youImage {
  width: 2em;
  height: 2em;
  border-radius: 50%;
}

.profile-image {
  width: 1.5em;
  height: 1.5em;
  margin-left: auto;
  margin-right: auto;
  border-radius: 50%;
}
.profile-pic {
  width: 10em;
  height: 10em;
  border-radius: 50%;
}
#profile-info {
  background-color: rgb(240, 240, 240);
  border-left: 0.5px solid rgb(190, 190, 190);
  padding: 40px;
  justify-content: center;
}
.profile-details {
  background-color: #fff;
  padding: 40px;
  border-radius: 3%;
  justify-content: center;
}
.caption {
  justify-content: center;
  text-align: center;
  margin: auto;
  font-weight: 700;
  font-stretch: extra-expanded;
  font-size: 1.2em;

  color: #777;
}
.captionName {
  color: rgb(47, 47, 47);
}
.informationTag {
  background-color: #fff;
  margin: 1em 0 0 0;
  padding: 15px;
  font-weight: 700;
  font-stretch: extra-expanded;
  font-size: 1.5em;
  color: rgb(47, 47, 47);
  border-radius: 3%;
}
.fileDownload {
  background-color: #fff;
  margin: 0.3em 0 1em 0;
  padding: 15px;
  font-weight: 700;
  font-stretch: extra-expanded;
  font-size: 1.5em;
  color: rgb(47, 47, 47);
  border-radius: 3%;
}
.downloadItems {
  padding: 4em 2em 5em 2em;
}
.faAngledown {
  padding: 0 0 0 8em;
}
.faAngleup {
  padding: 0 0 0 10em;
}
#app-wrapper {
  display: flex;
  background-color: #fff;
  border: 10px solid #fff;
  border-radius: 2%;
}

#chat-container {
  display: grid;
  grid:
    "search-container chat-title" 71px
    "conversation-list chat-message-list" 1fr
    "new-message-container chat-form" 78px
    / 275px 1fr;
  min-width: 800px;
  max-width: 1000px;
  max-height: 800px;
  flex: 1 0 0;
  height: 110vh;
  background: rgb(240, 240, 240);
  border-radius: 10px;
}
#search-container,
#conversation-list {
  background-color: rgb(240, 240, 240);
}

#search-container {
  display: flex;
  align-items: center;
  grid-area: search-container;
  padding: 0 20px;
  border-radius: 10px 0 0 0;
  box-shadow: 0 1px 3px -1px rgba(0, 0, 0, 0.75);
  z-index: 1;
}

#search-container input {
  flex: 1 0 0;
  color: #626262;
  outline: none;
  font-weight: bold;
  border-radius: 2px;
  height: 30px;
  border: 0;
  padding-left: 48px;
  padding-right: 20px;
  font-size: 1.4rem;
  background: url("./img/search/search.svg") no-repeat rgba(168, 168, 168, 0.3);
  background-size: 20px 20px;
  background-position: 15px center;
}

#search-container input::placeholder {
  color: rgb(129, 129, 129);
  font-weight: bold;
}

#conversation-list {
  grid-area: conversation-list;
  overflow-y: scroll;
  border-right: 1px solid rgb(212, 212, 212);
}

.conversation {
  display: grid;
  grid-template-columns: 40px 1fr max-content;
  grid-gap: 10px;
  color: rgb(47, 47, 47);
  font-size: 1.3rem;
  border-bottom: 1px solid rgb(160, 160, 160);
  padding: 20px 20px 20px 15px;
}

.conversation.active,
.conversation:hover {
  background: #fff;
}

.conversation:hover {
  cursor: pointer;
}

.conversation > img {
  grid-row: span 2;
  height: 40px;
  width: 40px;
  border-radius: 100%;
}

.title-text {
  font-weight: 600;
  font: 500;
  color: rgb(46, 46, 46);
  padding-left: 5px;
  white-space: nowrap;
  overflow-x: hidden;
  text-overflow: ellipsis;
}

.created-date {
  font-size: 1rem;
}

.conversation-message {
  grid-column: span 2;
  padding-left: 5px;
  white-space: nowrap;
  overflow-x: hidden;
  text-overflow: ellipsis;
}

#new-message-container {
  display: flex;
  align-items: center;
  grid-area: new-message-container;
  border-top: 1px solid rgba(0, 0, 0, 0.25);
  border-radius: 0 0 0 10px;
  padding: 0 15px;
}

#new-message-container a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  background: #eee;
  border-radius: 100%;
  color: rgb(108, 132, 204);
  text-decoration: none;
  font-size: 3.6rem;
}

#chat-title {
  background: #eee;
  border-bottom: 1px solid gray;
}
#chat-form {
  background: #eee;
  border-left: 1px solid rgb(212, 212, 212);
}

#chat-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-area: chat-title;
  color: rgb(24, 24, 24);
  font-size: 2rem;
  border-radius: 0 10px 0 0;
  box-shadow: 0 1px 3px -1px rgba(0, 0, 0, 0.75);
  padding: 0 20px;
}

#chat-title > img,
#chat-form > img {
  cursor: pointer;
}

#chat-title > img {
  width: 36px;
  height: 36px;
}

#chat-message-list {
  grid-area: chat-message-list;
  display: flex;
  flex-direction: column-reverse;
  padding: 0 20px;
  overflow-y: scroll;
}

.message-row {
  display: grid;
  grid-template-columns: 70%;
  margin-bottom: 20px;
}

.message-content {
  display: grid;
}

.you-message {
  justify-content: end;
}

.you-message .message-content {
  justify-items: end;
}

.other-message .message-content {
  grid-template-columns: 48px 1fr;
  grid-column-gap: 15px;
}

.message-row img {
  border-radius: 100%;
  grid-row: span 2;
}

.message-time {
  font-size: 1.3rem;
  color: #777;
}

.time-info {
  display: flex;

  font-size: 1.3rem;
  color: rgb(190, 190, 190);
}
.timer {
  flex-basis: inherit;
  justify-content: space-between;
  padding: 0 45px 0 0px;
}
.timmer2 {
  padding-left: 8em;
}
.timmer {
  padding-left: 18em;
}

.message-text {
  padding: 9px 14px;
  font-size: 1.6rem;
  margin-bottom: 5px;
}

.you-message .message-text {
  background: rgb(72, 107, 214);
  color: #eee;
  border: 1px solid rgb(72, 107, 214);
  border-radius: 14px 14px 0 14px;
}

.other-message .message-text {
  background: #fff;
  color: #111;
  border: 1px solid #ddd;
  border-radius: 14px 14px 14px 0;
}

#chat-form {
  display: flex;
  align-items: center;
  grid-area: chat-form;
  border-top: 1px solid rgba(0, 0, 0, 0.25);
  border-radius: 0 0 10px 0;
  padding-left: 42px;
  padding-right: 22px;
  width: 100%;
}

#chat-form i {
  position: absolute;
  color: rgb(51, 81, 170);

  font-size: 4.5em;
}

.icon {
  padding: 10px;
  color: white;
  min-width: 50px;
  text-align: center;
}
.type-message {
  width: 100%;
  padding: 10px;
  outline: none;
}

#chat-form > img {
  width: 32px;
  height: 32px;
  margin-right: 15px;
}

#chat-form input {
  flex: 1 0 0;
  outline: none;
  padding: 15px;
  padding-left: 5em;
  border: 2px solid #ddd;
  color: #330;
  border-radius: 6px;
  font-size: 1.4rem;
}

#chat-form input::placeholder {
  padding-left: 5em;
}

::-webkit-scrollbar {
  display: none;
}
